/* About.css */
.container {
    /* max-width: 1300px; */
    max-width: 100%;
    padding: 2%;
    margin-top: 3%;
    margin-bottom: 3%;
    transition: background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease;
}

.accordion-button {
    font-size: 1.2rem;
    font-weight: 500;
    transition: background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease;
}

.dark-mode .accordion-button {
    background-color: #282c34;
    color: #fff;
}

.accordion-body {
    font-size: 1rem;
    line-height: 1.6;
    transition: background-color 0.3s ease, color 0.3s ease;
}

.dark-mode .accordion-body {
    background-color: #282c34;
    color: #fff;
}

button {
    transition: background-color 0.3s ease, color 0.3s ease;
}

/* Responsive styles */
@media (max-width: 768px) {
    .container {
        padding: 15px;
    }

    .accordion-button {
        font-size: 1rem;
    }

    .accordion-body {
        font-size: 0.9rem;
    }
}